body {
  background-color: #c5c3c4;
  box-sizing: border-box;
  overflow: hidden; }

.save-score-container {
  background-color: rgba(0, 0, 0, 0.5);
  cursor: default;
  display: block;
  height: 100%;
  left: 0;
  position: fixed;
  text-align: center;
  top: 0;
  width: 100%;
  z-index: 999; }
  .save-score-container:before {
    content: '';
    display: inline-block;
    height: 100%;
    margin-right: -0.25em;
    vertical-align: middle; }
  .save-score-container.hidden {
    display: none; }
  .save-score-container .save-score-form {
    background-color: #fff;
    display: inline-block;
    padding: 15px;
    text-align: left;
    vertical-align: middle;
    width: 350px; }

.form-label {
  cursor: default; }

.form-input {
  text-transform: uppercase; }

.form-section {
  margin-bottom: 10px; }

.app {
  height: 100%;
  text-align: center; }

.hidden {
  visibility: hidden; }

.game-panel {
  color: #666;
  display: none;
  font-weight: bold;
  font-size: 18px;
  vertical-align: top; }

.game {
  display: inline-block;
  height: 100%; }

.high-score, .high-level {
  margin-top: 8px; }

@media all and (min-width: 975px) {
  body {
    padding: 20px; }
  .game-panel {
    display: inline-block; }
  .game {
    margin: 0 80px; } }

.no-animate {
  transition: none !important; }

.blur-fix {
  backface-visibility: hidden;
  transform: translateZ(0);
  transform-style: preserve-3d; }

.hover-zoom {
  transition: all ease-out .3s; }
  .hover-zoom:hover {
    transform: scale(1.125);
    transition: all ease-in-out .2s; }

.show-fade {
  opacity: 1;
  transition: opacity ease-in-out .75s; }
  .show-fade.hidden {
    opacity: 0; }

.show-fade-slow {
  opacity: 1;
  transition: opacity ease-in-out 1.25s; }
  .show-fade-slow.hidden {
    opacity: 0; }

.hide-fade {
  opacity: 1;
  transition: opacity ease-in-out .75s; }
  .hide-fade.hidden {
    opacity: 0;
    visibility: visible; }

.show-zoom {
  opacity: 1;
  transform: scale(1);
  transition: opacity ease-in-out .75s, transform ease-in-out .75s; }
  .show-zoom.hidden {
    opacity: 0;
    transform: scale(.75); }

.flip-card {
  backface-visibility: hidden;
  transition: transform .75s ease-in-out; }

.flip-left {
  transform: rotateY(-180deg); }

.flip-right {
  transform: rotateY(180deg); }

